.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

/* Header component */

.logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 4em;
  padding: 0.5em 1em 0.5em 0.5em;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.header {
  background-color: #e91e63;
  color: white;
  text-align: left;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5em;
  z-index: 99;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.app-title {
  font-size: 1.4em;
  font-family: 'Merriweather', serif;
  text-align: left;
  padding: 0.8em 0 0 0;
  margin: 0 0 0 0;
}

.app-slogan {
  font-size: 0.8em;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}

/* Main container */
main {
  margin-top: 5em;
  z-index: 10;
  display: -ms-inline-flexbox;
  display: inline-flex;
  height: calc(100vh - 6em);
}

/* Sidebar component */

.sidebar {

  width: 320px;
  background-color: #f8f8f8;
  font-family: 'Montserrat', sans-serif;
}

.search-input {
  width: 252px;
  height: 32px;
  border: 1px solid #bdbdbd;
  border-radius: 20px;
  margin: 1em;
  padding: 0 0 0 2em;
  background-image: url('./images/search.svg');
  background-repeat: no-repeat;
  background-position: 0.5em center;
  background-size: 1.2em;
  font-size: 1em;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  color: #6e6e6e;
}

.sidebar-heading {
  font-family: 'Montserrat', sans-serif;
  font-size: 0.6em;
  font-weight: 400;
  color: #373737;
  padding: 0 1em 0 2em;
}

.filter {
  display: -ms-inline-flexbox;
  display: inline-flex;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.6em;
  font-weight: 300;
  color: #6e6e6e;
  padding: 0 1em 0 2em;
}

.filter-item {
  padding: 0 1em 0 1em;
  width: 48px;
  text-align: center;
}

.location-list {
  list-style-type: none;
  font-family: 'Montserrat', sans-serif;
  font-size: 1em;
  font-weight: 300;
  color: #373737;
  padding: 0 1em 0 2em;
}

.location-item {
  padding: 0;
}

.list-item {
  margin: 4px 0 4px 0;
  font-size: 0.7em;
  font-family: 'Montserrat', sans-serif;
}

/* Map component */

.map {
  width: calc(100vw - 320px);
  height: 100%;
  z-index: 10;
  font-family: 'Montserrat', sans-serif;
}

.loading-message {
  font-size: 1em;
  font-family: 'Montserrat', sans-serif;
  font-weight: 200;
  color: #6e6e6e;
  text-align: center;
}

.marker {
  cursor: pointer;
}

.marker.pulse {
  -webkit-animation: pulse 0.5s ease 0s alternate 4;
          animation: pulse 0.5s ease 0s alternate 4;
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
}

@-webkit-keyframes pulse {
  to { -webkit-transform: scale(1.2); transform: scale(1.2);}
}

@keyframes pulse {
  to { -webkit-transform: scale(1.2); transform: scale(1.2);}
}

.info-window {
  position: absolute;
  text-align: center;
  background-color: #fff;
  padding: 0.5em;
  width: 220px;
  height: 240px;
  -webkit-transform: translate(-50%, 15%);
      -ms-transform: translate(-50%, 15%);
          transform: translate(-50%, 15%);
  cursor: default;
  background: #fff;
  position: relative;
  left: 25px;
  bottom: 260px;
  border-radius: 5px;
  -webkit-box-shadow: 0 2px 2px #bdbdbd;
          box-shadow: 0 2px 2px #bdbdbd;
}

.info-window #triangle {
   position: absolute;
   left: 50%;
   top: 100%;
   -webkit-transform: translate(-15px, 0);
       -ms-transform: translate(-15px, 0);
           transform: translate(-15px, 0);
   width: 45px;
   height: 10px;
   background-image: url('./images/shadow.png');
   background-repeat: no-repeat;
}

.info-window #triangle:before {
   content:"";
   position: absolute;
   -webkit-transform: translate(-25px, -6px);
       -ms-transform: translate(-25px, -6px);
           transform: translate(-25px, -6px);
   width: 0;
   height: 0;
   border-left: 10px solid transparent;
   border-right: 10px solid transparent;
   border-top: 15px solid #fff;
}

.info-window .close-window {
  position: absolute;
  background-image: url('./images/close.svg');
  background-color: inherit;
  top: 16px;
  right: 16px;
  width: 16px;
  height: 16px;
  font-size: 0;
  border: none;
  outline: none;
  cursor: pointer;
}

.info-title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  font-size: 1.25em;
  text-align: left;
  padding: 8px 0 0 12px;
  margin: 0 2em 0 0;
}

.info-address {
  width: 180px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  font-size: 0.8em;
  color: #6e6e6e;
  text-align: left;
  padding: 4px 12px 4px 12px;
  margin: 0 0 4px 0;
}

.info-image {
  height: 120px;
}

.info-foursquare {
  margin: 0.5em 0 0 0;
}

.info-rating {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 1.25em;
  color: #00b551;
}

.info-fullscore {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  font-size: 1em;
  color: #6e6e6e;
  margin: 0;
}

/* Footer component */

footer {
  width: 100vw;
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 20;
  background-color: #f48eB1;
  color: white;
  font-size: 0.8em;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  text-align: center;
}

/* Media queries */

@media screen and (max-width: 600px) {

  main {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 100vw;
    margin: 0;
  }

  .sidebar {
    -ms-flex-order: 2;
        order: 2;
    width: 100%;
    height: 100%;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
   }

   .search-input {
     width: calc(100vw - 6em);
   }

  .map {
    -ms-flex-order: 1;
        order: 1;
    width: 100vw;
    height: 50vh;
   }

  footer  {
    -ms-flex-order: 3;
        order: 3;
    visibility: hidden;
  }
}



